html {
  text-align: center;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.btn {
  background: #d87af0;
  background-image: -webkit-linear-gradient(top, #d87af0, #562c61);
  background-image: -moz-linear-gradient(top, #d87af0, #562c61);
  background-image: -ms-linear-gradient(top, #d87af0, #562c61);
  background-image: -o-linear-gradient(top, #d87af0, #562c61);
  background-image: linear-gradient(to bottom, #d87af0, #562c61);
  -webkit-border-radius: 39;
  -moz-border-radius: 39;
  border-radius: 39px;
  font-family: Arial;
  color: #ffffff;
  font-size: 40px;
  padding: 23px 34px 23px 34px;
  text-decoration: none;
}

.btn:hover {
  background: #e86dd6;
  background-image: -webkit-linear-gradient(top, #e86dd6, #db2994);
  background-image: -moz-linear-gradient(top, #e86dd6, #db2994);
  background-image: -ms-linear-gradient(top, #e86dd6, #db2994);
  background-image: -o-linear-gradient(top, #e86dd6, #db2994);
  background-image: linear-gradient(to bottom, #e86dd6, #db2994);
  text-decoration: none;
}
